export default {
  name: "crypto",
  data() {
    return {
      vip_id: undefined,
      crypto_conf: undefined,
      vip_config: undefined
    }
  },
  computed: {
    is_valid: function () {
      return this.crypto_conf && this.vip_config;
    },
    send_amount: function () {
      return (this.vip_config['amount'] / 6.3).toFixed(2);
    }
  },
  created() {

    this.vip_id = this.$route.query.vip_id;
    console.log('this.this.$route.query:', this.$route.query);
    this.request_pay();

  },
  methods: {
    request_pay: function () {


      this.$axios.get('/app/pay/crypto', {
        params: {
          'action': 'pay',
          'vip_id': this.vip_id
        }
      }).then((response) => {

        console.log('response:', response);
        this.crypto_conf = response.data.crypto_conf;
        this.vip_config = response.data.vip_config;

      }).catch((error) => {


      });
    }
  }
}
